import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

import Realistic from "./layout/results/testResult";

// using ES6 modules
import { HashRouter as Router, Route, Switch } from "react-router-dom";

//import {create as createJss} from 'jss'
//import jssDefaultPreset from 'react-jss/lib/jss'
import {JssProvider, jss} from 'react-jss'
import jssNested from 'jss-plugin-nested';
//const jss = jssDefaultPreset; // Fuck!
jss.use(jssNested()); // Change from jssNested to jssNested() save my ass, fuck!

function AppWraper(props)
{
    return (
        <JssProvider jss={jss}>
            <App {...props} />
        </JssProvider>
    )
}

function AppRouter()
{
    window.alert(process.env.PUBLIC_URL);
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <Switch>
                <Route exact path='/' component={AppWraper} />
                <Route exact path='/it' render={props => <AppWraper {...props} helmet={"it"} />} />
                <Route exact path='/testShare' component={Realistic} />
                {
                /*
                <Route exact path='/Realistic' component={Realistic} />
                <Route exact path='/Investigative' component={AppWraper} />
                <Route exact path='/Artistic' component={AppWraper} />
                <Route exact path='/Social' component={AppWraper} />
                <Route exact path='/Enterprising' component={AppWraper} />
                <Route exact path='/Conventional' component={AppWraper} />
                */
                }
            </Switch>
        </Router>
    )
}


const rootElement = document.getElementById("root");
if (rootElement.hasChildNodes()) {
    ReactDOM.hydrate(
    <AppRouter />, rootElement);
} else {
    ReactDOM.render(
    <AppRouter />, rootElement);
}

// ReactDOM.render(
// <JssProvider jss={jss}>
//     <App />
// </JssProvider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
