const publicUrl = process.env.PUBLIC_URL;
const locationUrl = window.location.origin.toString() + "/" + publicUrl;
const getTitle = helmet => {
    if(helmet == "it")
      return "your bad title";
    return "no title";
  };
  
  const getImage = (helmet) => {
    if(helmet == "it")
      return locationUrl + "images/results/test.jpg";
    return "no image";
  };
  
  const getDescription = helmet => {
    if(helmet == "it")
      return "You are sucks!";
    return "no description";
  };

  const getUrl = helmet => {
    return locationUrl  + "/" + helmet;
  }

  export {
      getTitle,
      getImage,
      getDescription,
      getUrl,
  }