import React, { useState } from 'react';
import BsButton from 'react-bootstrap/Button'
import injectSheet from 'react-jss';

const styles = {
    surveyButton: {
        width: 174,
        height: 51,
        backgroundColor: "#335ecf",
        borderColor: "#335ecf",
        objectFit: "contain",
        borderRadius: 25.5,
        fontFamily: "Noto Sans TC",
        fontSize: 24,
        fontWeight: "bold",
        fontStyle: "normal",
        fontStretch: "normal",
        letterSpacing: "normal",
        display: "inline-flex",
        justifyContent: "center",
    },
    centerDiv: {
    }
};

function Button({classes, style, children, onClick}) {
    return (
        <BsButton className={classes.surveyButton} style={style} variant="primary" onClick={onClick}>
            <span className={classes.centerDiv}>
                { children }
            </span>
        </BsButton>
    );
}

export default injectSheet(styles)(Button);