import React, { useState } from 'react';
import injectSheet from 'react-jss';
import Button from '../customs/button';
import './animate.css';
import { ReactComponent as ArrowIcon } from  './images/icon_arrow.svg';
import { ReactComponent as MainIcon } from  './images/pic_1_main page.svg';
//import { ReactComponent as Introduction } from  './images/pic_2_indroduction.svg';
import pic2 from  './images/pic_2_indroduction@3x.png';

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

const styles = {
    highlight: {
        height: "40px",
        background: "#e8eefe",
        display: "inline-block",
        position: "relative",
    },
    highlightOffset: {
        'margin-top': "-20px",
    },
    commonSection: {
        width: "40%",
        margin: 5, 
        padding: "1em",
        background: "white",
    },
    left: {
        float: "left",
        width: "25%",
    },
    right: {
        float: "right",
        width: "70%",
    },
    trickyTitle: {
        'min-height': "400px",
        'padding-left': "140px",
    },
    '@media (max-width: 1024px)': {
        trickyTitle: {
            'min-width': 0,
            'min-height': 0,
            'padding-left': 0,
        }
    },
    fontBg: {
        color: "transparent",
        float: "left",
        display: "inline-block",
        position: "absolute",
        "&:before": {
            content: '""',
            position: "absolute",
            width: "100%",
            height: "14px",
            top: "10px",
            left: "-5px",
            background: "#e8eefef6",
            zIndex: "-1"
        },
    }
};


function Intro({classes, onNext}) {
    const [page, setPage] = useState(0);
    // Declare a new state variable, which we'll call "count"
    return (
        <div>
            <Container style={{ overflow: "hidden", maxWidth: "100%", paddingTop: 40, paddingLeft: 0}}>
                {/*
                <Row>
                    <p>You clicked {page} times</p>
                </Row>
                */
                }
                <Row>
                    <Col sm={4} style={{display: "flex", alignItems: "center"}}>
                        <div className={classes.trickyTitle}>
                        <div className={classes.highlight} style={{marginBottom: 48, fontWeight: "bold"}}>
                            <h1 className={classes.highlightOffset}>工作偏好測驗</h1>
                        </div>
                        <br/>
                        <div className={classes.highlight} style={{fontWeight: "bold"}}>
                            <h2 className={classes.highlightOffset} style={{marginTop: -14}}>尋找我的職業生涯</h2>
                        </div>
                        </div>
                    </Col>
                    <Col sm={8}><MainIcon /></Col>
                </Row>
                <Row>
                    <Col sm={7}>
                        { /* <Introduction style={{display: "block", float: "right"}}/> */}
                        <img  src={pic2} style={{display: "block", float: "right", maxWidth: 956, maxHeight: 551}} alt=""/>
                    </Col>
                    <Col sm={5} style={{display: "flex", alignItems: "center"}}>
                        <div style={{position: "relative", zIndex: 1, minHeight: 200, margin: 5, marginBottom: "20px", padding: 15}}>
                            <div className={classes.fontBg}>今天，你加入了這家名為「BCC」的新創公司」，</div>
                            <div className={classes.highlight} style={{height:16, fontSize: 16, fontWeight: "bold", lineHeight: 1.76, background: "transparent"}}>
                                <p style={{textAlign: "left"}}>
                                今天，你加入了這家名為「BCC」的新創公司」，<br />
                                公司沒有安排特定的工作或崗位給你，而是讓你自由選擇想做的工作。無論你選擇什麼工作，你工作的時間和報酬都是一樣的。請依照你的喜好，思考什麼工作內容是最你想做的！<br />
                                <br />（在以下情境中，你只需要跟著自己的直覺來選擇，不必考慮是否有能力做到喔！）
                                </p>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>
            {/*
            <div>
                <div className={`${classes.commonSection} ${classes.left}`}>
                    <div className={classes.highlight}>
                        工作偏好設定
                    </div>
                    <br/>
                    <div className={classes.highlight}>
                        尋找我的職業生涯
                    </div>
                </div>
                <div className={`${classes.commonSection} ${classes.right}`}>
                    <MainIcon />
                </div>
            </div>
            */
            }
            <Button onClick={() => onNext()} style={{marginBottom: 20}}>
                開始作答
                <ArrowIcon style={{marginLeft: 4}} />
            </Button>
        </div>
    );
}

export default injectSheet(styles)(Intro);
