import React, { useState, useContext } from 'react';
import injectSheet from 'react-jss';

import { FacebookShareButton, FacebookIcon } from 'react-share';
import {Helmet} from "react-helmet";
import {withRouter, Redirect} from 'react-router-dom';

import {getUrl, getTitle, getImage, getDescription} from '../../HelmetMeta';


function GameResult(props) {
    const Title = "God";
    //const shareUrl = !process.env.PUBLIC_URL ? "https://kingkong.com.tw" : process.env.PUBLIC_URL;
    return (
        <div>
             <Helmet>
                <title>{Title}</title>
                <meta property="og:url" content={getUrl("it")} />
                <meta property="og:type" content="article" />
                <meta property="og:title" content={getTitle("it")} />
                <meta property="og:description" content={getDescription("it")} />
                <meta property="og:image" itemprop="image" content={getImage("it")} />
                {
                /*
                    <meta property="og:url" content={shareUrl} />
                    <meta property="og:type" content="website" /> />
                    <meta property="og:title" content="Your Bad Title" />
                    <meta property="og:description" content="You are sucks!" />
                    <meta property="og:image" content="http://static01.nyt.com/images/2015/02/19/arts/international/19iht-btnumbers19A/19iht-btnumbers19A-facebookJumbo-v2.jpg" />
                */
                }
            </Helmet>
            <FacebookShareButton
                url={getUrl("it")}>
                <FacebookIcon
                size={32}
                round />
            </FacebookShareButton>
        </div>
    );
}

export default withRouter(props => <GameResult {...props}/>);